import { stopSubmit } from "redux-form";
import { authAPI } from "../api/api";


const SET_USER_DATA = 'SET_USER_DATA';




let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false

}
const AuthReducer = (state = initialState, action) => {
    switch (action.type) {  //    reducer   state,    actio  n
        case SET_USER_DATA:

            return {
                ...state,
                ...action.data,
            }
        default:                                                 //  action    currentPage
            return state;                                        //    AC   currentPage                 
        //   state                                                                   
    }

}
//----------------
//ActionCreators
//-----------------
export const setAuthUserData = (userId, login, email, isAuth) => ({ type: SET_USER_DATA, data: { userId, login, email, isAuth } })//    -   -      -.  ,         

export const getAuthUserData = () => async (dispatch) => { // -         
    const response = await authAPI.me();
    if (response.data.resultCode === 0) {
        let { id, login, email } = response.data.data;
        dispatch(setAuthUserData(id, login, email, true));
    }
}


export const login = (email, password, rememberMe) => async (dispatch) => {
    let response = await authAPI.login(email, password, rememberMe);
    if (response.data.resultCode === 0) {
        dispatch(getAuthUserData())
    } else {
        let message = response.data.messages.length > 0 ? response.data.messages[0] : 'Some error'
        dispatch(stopSubmit('login', { _error: message }));
    }
}

export const logout = (email, password, rememberMe) => async(dispatch) => {
    let response = await authAPI.logout(email, password, rememberMe);
        if (response.data.resultCode === 0) {
            dispatch(setAuthUserData(null, null, null, false))
        }
    
}




export default AuthReducer;